@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 28px 0 20px;
    }

    .mkdf-comment {

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 28px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        display: table-cell;
        top: 5px;
        left: 0;
        width: 110px;
        height: 110px;

        img {
            display: block;
            border-radius: 50%;
            border: 1px solid $default-border-color;
            padding: 3px;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 40px;
        min-height: 110px;
        display: table-cell;
        vertical-align: middle;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            width: auto;
            vertical-align: bottom;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin: 3px 0 0 10px;
            color: $first-main-color;

            &:hover {
                color: $default-hover-color;
            }
        }

        .comment-reply-link {
            margin-top: 5px;
            &:after {
                content: "\f4c7";
                font-family: "Ionicons";
                position: relative;
                font-size: 20px;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            margin: 2px 0 -2px 7px;
            width: auto;
            display: inline-block;
            vertical-align: middle;
        }

        .mkdf-text-holder p {
            margin: 17px 0 7px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 55px 0 35px;
    }

    .comment-reply-title {
        margin: 0 0 16px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'] {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonOutlineColor();
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;

    cursor: pointer;

    &:hover {
        color: $default-background-color;
        background-color: $default-heading-color;
    }
}

input.wpcf7-form-control.wpcf7-submit {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonOutlineColor();
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;

    cursor: pointer;

    &:hover {
        color: $default-background-color;
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    height: 85px;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.mkdf-contact-form {
    .wpcf7-form-control.wpcf7-text {
        margin-bottom: 20px;
    }

    .wpcf7-form-control.wpcf7-textarea {
        height: 90px;
        margin-bottom: 30px;
    }
}

.mkdf-two-columns-form-with-space {

    .mkdf-column-left{
        padding: 0 5px 0 0 !important;

        input {
            border: 1px solid #e1e1e1 !important;
            padding: 5px 20px 5px 15px !important;
            height: 50px;
        }
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 5px;
            margin: 0;
            color: $default-heading-color;
            fill: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(all .3s ease-out);

            svg {
                @include mkdfTransition(all .3s ease-out);
            }

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 25px;

            &:hover {

                svg {
                    @include mkdfTransform(translateX(-5px));
                }
            }
        }

        .owl-next {
            right: 25px;

            &:hover {

                svg {
                    @include mkdfTransform(translateX(5px));
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 25px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 7px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 9px;
                height: 9px;
                border-radius: 50%;
                background-color: transparent;
                border: 1px solid #fff;
                box-sizing: border-box;

                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-searchform {
            display: inline-block;
            width: 400px;
            margin-top: 7px;

            .input-holder {
                border-bottom: 1px solid $default-border-color;
                padding: 0 13px;
                box-sizing: border-box;
            }

            .search-field,
            .mkdf-search-submit{
                background-color: transparent;
                border: none;
                color: #fff;
            }

            .search-field {
                font-family: $default-text-font;
                font-size: 16px;
                font-weight: 400;
                color: $default-heading-color;
                padding: 16px 0;
                box-sizing: border-box;
                width: calc(100% - 30px);
                -webkit-appearance: none;

                &:focus {
                    outline: none;
                }
                
                @include mkdfPlaceholder {
                    color: $default-heading-color;
                }
            }

            .mkdf-search-submit {
                font-size: 22px;
                cursor: pointer;
                width: 28px;
                padding: 10px 0;
                float: right;
                margin-top: 5px;

                i:before {
                    font-weight: 900;
                }
            }
        }

        .mkdf-404-subtitle {
            margin: 19px 0 0;
        }

        .mkdf-404-text {
            margin: 10px auto 0;
            max-width: 400px;
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: auto;
            min-height: 100vh;
            margin-top: -70px!important; /* 70px is mobile header height */

            .mkdf-content-inner {
                min-height: 100vh;
            }
        }

        &.admin-bar {

            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }

    @include phone-landscape {
        .mkdf-page-not-found {
            .mkdf-404-searchform {
                width: 280px;
            }
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 100000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(opacity 0.3s ease);
    }

    .mkdf-btn-text {
        position: relative;
        z-index: 23;
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 25px;
        text-align: center;
        color: #fff;
        background-color: #212121;
        border: 1px solid #fff;
        overflow: hidden;

        @include mkdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        i {
            @include mkdfTransition(.15s ease .05s);
        }

        &:before {
            display: block;
            position: absolute;
            bottom: initial;
            top: 0;
            height: 0%;
            width: 100%;
            content: '';
            color: #000 !important;
            background: #fff;
            @include mkdfTransition(all .3s cubic-bezier(0.42, 0, 0.58, 1));
        }
    
        &:hover {
            color: #000 !important;
            background-color: transparent;
    
            &:before {
                bottom: 0;
                top: initial;
                height: 100%;
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
        z-index: 23;
        position: relative;
        font-size: 20px;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

.mkdf-btn-outline-form  {
    background-color: transparent !important;
    color: #fff;
    cursor: pointer;
    position: relative;
    padding: 0 !important;
    @include mkdfTransition(all .3s cubic-bezier(0.42, 0, 0.58, 1));

    &:before {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 0;
        content: '';
        color: #000 !important;
        background: #fff;
        @include mkdfTransition(all .3s cubic-bezier(0.42, 0, 0.58, 1));
    }

    &:hover {
        color: #000 !important;
        background-color: transparent;

        &:before {
            left: 0%;
            right: auto;
            width: 100%;
        }
    }

    .mkdf-btn-text {
        position: relative;
        z-index: 23;
    }

    .ajax-loader {
        position: absolute;
        top: 16px;
        right: 5px;
    }
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */