footer {
    // widgets spacing
    $widget-bottom-margin: 15px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;
    $widget-title-margin: 15px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: $default-text-color;
    $widget-heading-color: #ffffff;

    // links
    $widget-link-color: #eeeeee;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .widget{
        &.widget_text {
            p {
                &:first-child {
                    padding-top: 0;
                }
            }

            a:hover {
                color: #959595;
            }
        }
    }

    div[id*="monster"] {
        .mkdf-widget-title {
            font-size: 21px;
            margin-top: 50px;
        }
    }

    .mkdf-footer-bottom-holder {
        .mkdf-grid-col-4{
            &:first-of-type {
                text-align: left;
            }
            &:last-of-type {
                text-align: right;
            }
        }
        .widget {
            margin: 0;
        }
    }
}